import { Commands, Controller } from '@ali/4ever-cangjie';
import { mergeData } from '../../utils';

export default function toggleFindAndReplace(
  controller: Controller,
  forceVisible?: boolean,
) {
  const { data } = controller.value;
  const visible = data.findAndReplaceVisible || false;
  const newVisible = forceVisible !== undefined ? forceVisible : !visible;

  mergeData(controller, {
    findAndReplaceVisible: newVisible,
  });
  controller.command(newVisible ? Commands.blur : Commands.focus);

  return controller;
}
